import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GoogleAnalytics } from '@ionic-native/google-analytics';
import { HomePage } from '../home/home';

/**
 * Generated class for the PlacePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: 'page-place',
  templateUrl: 'place.html',
})
export class PlacePage {

  place: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public ga: GoogleAnalytics) {
  	this.place = navParams.data;
    this.place.openDays = [];
  	this.ga.startTrackerWithId('UA-112680953-1')
      .then(() => this.ga.trackView('place'))
      .catch(e => console.log('Error starting GoogleAnalytics', e));
  }

  ionViewDidLoad() {
    // const navStyle = window.getComputedStyle(document.getElementById('navbar'));
    // console.log(navStyle.getPropertyValue('height'));

    const currentDate = new Date();
    console.log(currentDate.getDay());

    for (let i = 1; i <= 7; i++) {
      if (this.place.properties[`days${i}`]) {
        this.place.openDays.push(i);
      }
    }
    console.log(this.place.openDays);
  }

  closePlace() {
    this.navCtrl.push(HomePage, {});
  }
}



